.end-label {
  margin-top: 6px;
}

#sanil_ssi_db_selected_icons {
  display: none !important;
}

#settings-tabs {
  background: #B8B8B8;
  border: solid 1px #929292;
  height: 42px;
}
#settings-tabs li {
  border: solid 1px #929292;
  margin: 3px 0 0 7px;
  float: left;
}
#settings-tabs a {
  text-decoration: none;
  font-size: 16px;
  color: #23282D;
  display: block;
  padding: 10px 25px;
  background: #dcdcdc;
  transition: 0.4s;
}
#settings-tabs a:hover {
  background: #fff;
}
#settings-tabs li.active {
  border-bottom-color: #F1F1F1;
}
#settings-tabs li.active a {
  background: #F1F1F1;
}

#settings-wrapper *:not(i) {
  font-family: "Roboto";
}
#settings-wrapper input.number {
  width: 75px;
  float: left;
  margin-right: 5px;
}
#settings-wrapper [type=checkbox] {
  float: left;
  margin-right: 15px;
  margin-top: 7px;
}
#settings-wrapper .mb-50 {
  margin-bottom: 50px;
}
#settings-wrapper .section.cards .section-title {
  font-size: 17px;
  color: #23282D;
  margin-bottom: 15px;
}
#settings-wrapper .section.cards .section-contents {
  background: #fff;
  padding: 20px;
  overflow-x: auto;
}
#settings-wrapper .icons-search-wrapper {
  background: #F6F6F6 url(../../images/search.svg) no-repeat;
  background-position: calc(100% - 13px) 9px;
  margin-bottom: 20px;
}
#settings-wrapper .icons-search-wrapper input {
  padding: 4px 45px 4px 10px;
  border: solid 1px #B0CFE1;
  background: transparent;
  font-size: 16px;
  color: #000;
  font-family: roboto;
  font-weight: 300;
  border-radius: 0;
  width: 100%;
}

#available-icons-container {
  overflow: hidden;
  margin-bottom: -20px;
}
#available-icons-container a.icon-wrapper {
  float: left;
  margin: 0 10px 20px 0;
}
#available-icons-container a.icon-wrapper .icon-holder {
  display: flex;
  width: 85px;
  height: 60px;
  box-sizing: border-box;
  border: 1px solid #ededed;
  outline: 2px solid transparent;
  font-size: 30px;
  text-align: center;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #000;
  margin: 2px;
}
#available-icons-container a.icon-wrapper:hover, #available-icons-container a.icon-wrapper.selected {
  cursor: pointer;
}
#available-icons-container a.icon-wrapper:hover .icon-holder, #available-icons-container a.icon-wrapper.selected .icon-holder {
  border-color: #00A7F6;
  outline-color: #00A7F6;
}
#available-icons-container a.icon-wrapper:hover .label, #available-icons-container a.icon-wrapper.selected .label {
  color: #00A7F6;
}
#available-icons-container a.icon-wrapper.selected {
  cursor: not-allowed;
}

#selected-icons-container {
  margin-bottom: -20px;
}
#selected-icons-container .error-msg {
  color: red;
  clear: both;
  margin-left: 124px;
  padding: 0 0 15px;
  display: none;
}
#selected-icons-container .icon-row {
  overflow: hidden;
  margin-bottom: 20px;
  background: none;
  border: 0;
}
#selected-icons-container .icon-row .icon-holder {
  display: flex;
  width: 60px;
  height: 60px;
  box-sizing: border-box;
  border: 1px solid #ededed;
  font-size: 20px;
  text-align: center;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #000;
  float: left;
  margin-right: 25px;
  margin-bottom: 4px;
  border-radius: 50%;
  box-shadow: 0px 2px 5px #e8e8e8;
}
#selected-icons-container .icon-row .drag,
#selected-icons-container .icon-row button.remove-item-btn,
#selected-icons-container .icon-row button.more-options-btn {
  cursor: pointer;
  height: 38px;
  position: relative;
  float: left;
  margin-top: 12px;
  margin-right: 25px;
}
#selected-icons-container .icon-row .drag {
  background: url(../../images/drag.svg) no-repeat center;
  display: block;
  border: none;
  text-indent: -99999px;
  cursor: move;
  width: 12px;
}
#selected-icons-container .icon-row button.drag:focus,
#selected-icons-container .icon-row button.drag:active {
  box-shadow: none !important;
  border: 0;
  outline: 0;
}
#selected-icons-container .icon-row .url-input {
  border: solid 1px #A6A6A6;
  border-radius: 3px;
  font-size: 14px;
  color: #23282D;
  font-family: roboto;
  font-weight: 300;
  padding: 4px 10px;
  height: 38px;
  float: left;
  margin-top: 12px;
  margin-right: 25px;
  width: calc(100% - 87px - 37px - 152px - 68px);
}
#selected-icons-container .icon-row .more-options-btn {
  background: none;
  border: solid 1px #A6A6A6;
  padding-right: 30px;
  padding-left: 15px;
  border-radius: 3px;
  width: 127px;
}
#selected-icons-container .icon-row .more-options-btn:after {
  content: "";
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 6px solid black;
  top: 16px;
  position: absolute;
  right: 15px;
}
@keyframes flashing_btn_anim {
  0% {
    background: none;
  }
  49% {
    background: yellow;
  }
  100% {
    background: none;
  }
}
#selected-icons-container .icon-row .more-options-btn.flashing-anim {
  animation: flashing_btn_anim 0.8s infinite;
  animation-iteration-count: 2;
  animation-delay: 0.5s;
}
#selected-icons-container .icon-row .remove-item-btn {
  background: none;
  border: none;
  font-size: 12px;
  padding: 0px 0 0;
  color: #909090;
  margin-right: 0 !important;
  width: 43px;
}
#selected-icons-container .icon-row .remove-item-btn:before {
  content: "";
  height: 18px;
  background: url('data:image/svg+xml;utf8,<svg width="15" height="18" viewBox="0 0 15 18" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1.70203 16C1.70203 17.1 2.60203 18 3.70203 18H11.702C12.802 18 13.702 17.1 13.702 16V4H1.70203V16ZM14.702 1H11.202L10.202 0H5.20203L4.20203 1H0.702026V3H14.702V1Z" fill="black"/></svg>') no-repeat center;
  display: block;
}
#selected-icons-container .icon-row .remove-item-btn:hover {
  color: red;
}
#selected-icons-container .icon-row .remove-item-btn:hover:before {
  background-image: url('data:image/svg+xml;utf8,<svg width="15" height="18" viewBox="0 0 15 18" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1.70203 16C1.70203 17.1 2.60203 18 3.70203 18H11.702C12.802 18 13.702 17.1 13.702 16V4H1.70203V16ZM14.702 1H11.202L10.202 0H5.20203L4.20203 1H0.702026V3H14.702V1Z" fill="red"/></svg>');
}
#selected-icons-container .more-options-container {
  clear: both;
  overflow: hidden;
  background: #f7f7f7;
  margin-left: 124px;
  border-radius: 3px;
  padding: 0px 20px 20px;
}
#selected-icons-container .more-options-container .title {
  border-bottom: solid 1px #c3c3c3;
  padding-bottom: 5px;
  margin-bottom: 25px;
  font-size: 14px;
  color: #3F51B5;
}
#selected-icons-container .more-options-container .title button {
  float: right;
  margin-top: -2px;
  font-size: 12px;
  cursor: pointer;
}
#selected-icons-container .more-options-container label {
  width: 200px;
  float: left;
  font-weight: 600;
  margin-top: 6px;
}
#selected-icons-container .more-options-container .tooltip-label {
  width: 300px;
  max-width: 100%;
}
#selected-icons-container .more-options-container .moc-input-wrapper {
  float: left;
  width: calc(100% - 200px);
}
#selected-icons-container .more-options-container .moc-input-wrapper.has-color-picker {
  margin-bottom: -20px;
}
#selected-icons-container .more-options-container .form-group {
  overflow: hidden;
  margin-bottom: 20px;
  padding: 5px 1px 0px;
}
#selected-icons-container .more-options-container .colorpicker-group {
  float: left;
  width: 25%;
  min-width: 200px;
  max-width: 270px;
  margin-bottom: 20px;
}
#selected-icons-container .more-options-container .colorpicker-group p {
  margin: 0 0 3px;
  font-weight: 600;
}
#selected-icons-container .more-options-container button.remove-item {
  display: none;
}

@media (max-width: 767px) {
  #selected-icons-container .icon-row .url-input,
#selected-icons-container .icon-row button.more-options-btn {
    width: calc(100vw - 185px);
    margin: 0 0 10px;
  }
  #selected-icons-container .icon-row .remove-item-btn {
    display: none;
  }
  #selected-icons-container .error-msg {
    margin: 0;
  }
  #selected-icons-container .more-options-container {
    width: calc(100vw - 82px);
    margin: 0;
    padding: 0 10px;
  }
  #selected-icons-container .more-options-container label {
    width: 100%;
    margin-bottom: 5px;
  }
  #selected-icons-container .more-options-container .moc-input-wrapper {
    margin-top: 6px;
    width: 100%;
  }
  #selected-icons-container .more-options-container button.remove-item {
    margin: 0 0 15px;
    display: block;
    width: 100%;
    padding: 7px 0;
  }
}